import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';

import Container from "@material-ui/core/Container";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";

import history from "../history";
import historyPath from "../historyPath";
import Styles from "../styles";
import dict from "../dictionary";

import { removeToken } from "../functions/auth";
import { getLanguage } from "../reducers/languageReducer";

function MainPage(props) {
    const { language } = props;
    const classes = Styles();

    const handleClickDreams = () => {
        history.push(historyPath.Dreams);
    };

    const handleClickAddDream = () => {
        history.push(historyPath.AddDream);
    };

    const handleClickTechnics = () => {
        history.push(historyPath.Technics);
    };

    const handleLogout = () => {
        removeToken();
    };

    return (
        <div className={classes.root}>
            <Container>
                <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="stretch"
                    spacing={1}
                >
                    <Grid item>
                        <Button fullWidth size="small" variant="outlined" color="primary" onClick={handleClickDreams}>
                            {dict[language].buttons.MyDreams}
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button fullWidth size="small" variant="outlined" color="primary" onClick={handleClickAddDream}>
                            {dict[language].buttons.AddDream}
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button fullWidth size="small" variant="outlined" color="primary" onClick={handleClickTechnics}>
                            {dict[language].buttons.Technics}
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button fullWidth size="small" variant="outlined" color="primary" onClick={handleLogout}>
                            {dict[language].buttons.LogOut}
                        </Button>
                    </Grid>
                </Grid>
            </Container>
        </div>
    );
}

MainPage.propTypes = {
    language: PropTypes.string.isRequired
};

const mapStateToProps = store => {
    return {
        language: getLanguage(store)
    }
};

const mapDispatchToProps = (dispatch) => bindActionCreators({
}, dispatch)

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MainPage);